@font-face {
  font-family: "Atkinson Hyperlegible", sans-serif;
  src: url(../fonts/AtkinsonHyperlegible-Regular.ttf);
}

@font-face {
  font-family: "digital-7";
  src: url(../fonts/digital-7.ttf);
}

@import url("https://fonts.googleapis.com/css2?family=Gemunu+Libre&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Atkinson Hyperlegible", sans-serif;
}

body {
  background-image: url("../img/gridBG.gif");
  background-color: #000000;
  animation-name: open;
  animation-duration: 1s;
  overflow-y: hidden;
}

@keyframes open {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 100%;
  }
}

@media screen and (max-width: 767px) {
  /* Sidebar */

  .sidebar {
    position: fixed;
    display:inline;
    bottom: 1;
    left: 0;
    background-color: black;
    height: 20%;
    width: 100%;
  }

  .sidebar .logo-details {
   display: none;
  }

  .sidebar .logo-details img {
    display: none;
  }

  /* Scrollbar */

  ::-webkit-scrollbar {
    width: 0px;
  }

  ::-webkit-scrollbar-track {
    background: #000000;
  }

  ::-webkit-scrollbar-thumb {
    background: magenta;
    border-radius: 10px;
    padding: 10px;
  }

  ::-webkit-scrollbar-thumb:hover {
    background: white;
    transition: 0.5s ease;
  }

  /* Radio Collection */

  .nav-links {
    padding: 0px;
    height: 80%;
  }

  .plSel {
    padding: 10px;
  }

  .plSel:active {
    font-weight: bold;
  }

  .plTitle .plName {
    display: none;
  }

  .plSel .plItem .plTitle img {
    object-fit: cover;
    width: 50px;
    height: 50px;
    margin: 8px;
    border: 1px solid white;
  }

  /* Playbar */

  .playbar {
    background-color: black;
    position: fixed;
    display: inline;
    bottom: 0;
    width: 100%;
    height: 50%;
  }

  .playbar .details-1 .radioName {
    font-family: "Gemunu Libre", sans-serif !important;
    color: lime;
    text-decoration: none;
  }

  .playbar .details-1 .currently {
    font-size: 10px;
    margin-top: 3px;
    margin-bottom: 3px !important;
    font-family: "Gemunu Libre", sans-serif !important;
    color: lime;
  }

  .playbar .details-2 {
    color: white;
    font-size: 15px;
    margin-top: 0px;
  }

  .logoThumb {
    background-image: url(../img/staticTV.gif);
    background-size: cover;
  }

  .logoThumb.col {
    padding: 0px;
  }

  /* Controls */

  @keyframes lightScreenMove {
    0% {
      top: 30px;
      transform: scale(1, 0.5);
    }
    25% {
      top: -17px;
      transform: scale(1, 0);
    }
    50% {
      top: -17px;
      transform: scale(1, 0);
    }
    90% {
      top: 30px;
      transform: scale(1, 0);
    }
    100% {
      top: 30px;
      transform: scale(1, 0.5);
    }
  }

  .controls {
    padding: 7px;
  }

  .player {
    padding-right: 0px !important;
    background-image: url(../img/displayHorizonLines.png);
    background-size: cover;
  }

  .time {
    padding-top: 8px;
    padding-bottom: 7px;
    padding-left: 10px;
    padding-right: 10px;
  }

  .time .current {
    font-family: "Gemunu Libre", sans-serif !important;
    color: lime;
  }

  .player .play svg {
    width: 40px !important;
    height: 40px !important;
  }

  .volume-container {
    padding-top: 6px;
    padding-bottom: 8px;
  }

  .player .volume-icon svg {
    width: 30px !important;
    height: 30px !important;
  }

  .radioControls {
    position: absolute;
    z-index: 1;
  }

  /* .lightScreen {
    position: absolute;
    background-color: rgba(255, 255, 255, 0.212);
    width: 205px;
    height: 60% !important;
    animation-name: lightScreenMove;
    animation-duration: 60s;
    animation-iteration-count: infinite;
    z-index: 0;
  } */

  audio::-webkit-media-controls-enclosure {
    background-color: rgba(0, 0, 0, 0);
  }

  audio::-webkit-media-controls-panel {
    background-color: none;
  }

  audio::-webkit-media-controls-play-button {
    background-image: url("../img/play-circle.svg");
    background-size: 35px;
    min-width: 50px;
    height: 35px;
  }

  audio::-webkit-media-controls-current-time-display {
    color: lime;
    font-family: "Gemunu Libre", sans-serif !important;
  }

  audio::-webkit-media-controls-time-remaining-display {
    display: none;
  }

  audio::-webkit-media-controls-timeline {
    display: none;
  }

  audio::-webkit-media-controls-volume-control-container {
    margin-left: 10px;
  }

  audio::-webkit-media-controls-volume-control-hover-background {
    display: none;
  }

  audio::-webkit-media-controls-mute-button {
    background-image: url(../img/volume-up-fill.svg);
    background-size: 20px;
  }

  /* Clock */

  .clock {
    background-image: url(../img/displayHorizonLines.png);
    background-size: contain;
    padding-left: 0px !important;
  }

  .clock div {
    padding-top: 5px;
  }

  .clock span {
    font-family: "digital-7" !important;
    color: lime;
    font-size: 30px;
  }

  /* .lightScreen-2 {
    position: absolute;
    background-color: rgba(255, 255, 255, 0.212);
    width: 205px;
    height: 60% !important;
    animation-name: lightScreenMove;
    animation-duration: 60s;
    animation-iteration-count: infinite;
    z-index: 0;
  } */

  /* Audio visualizer */

  .displayBg canvas#audio-canvas {
    position: absolute;
    bottom: 55px;
    right: 0;
    z-index: -1;
  }
}

